import React from 'react'
import { SearchBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

const Search =(props)=>{
    return(
        <div>
            <SearchBar
                placeholder='宝贝'
                showCancelButton
                onClear={() => {
                    props.history.push('/Home');
                }}
            >
            </SearchBar>
        </div>
    )
}
export default withRouter(Search)