import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye1'></i>                           
                        }
                        selected={ pathname === '/home' }
                        onPress={ () => {
                            this.props.history.push('/home');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-favourited'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        title="收藏"
                        key="shoucang"
                        selected={ pathname === '/shoucang' }
                        onPress={ () => {
                            this.props.history.push('/shoucang')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-74wodedingdan'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-icon'></i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ pathname === '/dingdan' }
                        onPress={ () => {
                            this.props.history.push('/dingdan')
                        } }
                        data-seed="logId1"
                    >
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode'></i> }
                        selectedIcon={
                            <i className='iconfont icon-wode'></i>
                        }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/my' }
                        onPress={ () => {
                            this.props.history.push('/my')
                        } }
                        data-seed="logId1"
                    >     
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);