import React from 'react'
import { connect } from 'react-redux';
import Mytab from './Mytab';

const Shoucang = (props) => {
    return (
        <div>
            {
                props.select.map(val => (
                    <div style={{
                        display:"inline-block",
                        marginTop:"15px",
                        width:"160px",
                        height:"230px",
                        marginLeft:"10px",
                        borderRadius:"8px",
                        backgroundColor:'white'
                    }}>
                        <img
                            src={val.img}
                            alt=""
                            style={{ 
                            width: '150px', 
                            verticalAlign: 'top',
                            marginBottom:'5px',
                            marginLeft:'5px'
                            }}
                        />
                        <p style={{marginLeft:"15px"}}>{val.title}</p>
                        <p style={{marginLeft:"15px"}}>${val.price}</p> 
                        <button
                            onClick = {()=>{
                                props.dispatch({
                                    type:'DELETE',
                                    index:props.location.id
                                })
                            }}
                        >取消收藏</button>
                    </div>  
                )) 
            }
        <Mytab/>
        </div>
    )
}
const mapStateToProps = (state) => ({
    select: state.select
})
  
export default connect(mapStateToProps)(Shoucang);