import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import {dataList} from './actionCreators'
import { Carousel } from 'antd-mobile'
import { NavBar } from 'antd-mobile'
import Mytab from './Mytab'
import Things from './Things'

class Home extends React.Component { 
  constructor(props){
    super(props);
  }
    state = {
      data: ['1', '2', '3','4','5','6'],
      imgHeight: 176,
    }
    componentDidMount() {
      setTimeout(() => {
        this.setState({
          data: [
            'gw.alicdn.com/imgextra/i4/45/O1CN01rNyxVF1CCeS74h08l_!!45-0-ppp-picassogw', 
            'img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000002036/O1CN01X2yV8j1QuX1OpAltW_!!6000000002036-0-octopus', 
            'gw.alicdn.com/imgextra/i4/86/O1CN01Cf8h5C1CVQh3rmEtC_!!86-0-lubanu',
            'img.alicdn.com/imgextra/i2/46/O1CN01As2UmA1CD6qjTAKhL_!!46-0-luban', 
            'gw.alicdn.com/imgextra/i4/45/O1CN01rNyxVF1CCeS74h08l_!!45-0-ppp-picassogw', 
            'gw.alicdn.com/imgextra/i4/80/O1CN01ZYU2PW1CSgJhCSOMQ_!!80-0-lubanu'
          ],
        });
      }, 100);
    }
    render() {
      return (
        <div>
          <NavBar
            style={{
              backgroundColor:'rgb(255,133,42)',
              position: 'fixed',
              width:'100%',
              top: 0,
              zIndex:'2'
            }}
            mode="light"
            icon={
              <i class='iconfont icon-taobao' 
              style={{
                backgroundColor: 'rgb(255,133,42)',
                color:'white',
              }}
              ></i>  
            }
           
            rightContent={[
              <div
                style = {{
                  width:'280px',
                  height:'25px',
                  backgroundColor:'rgb(255,78,34)',
                  borderRadius:'4px'
                }}
              >
                <i class='iconfont icon-fangdajing' 
                  style={{
                    backgroundColor: 'rgb(255,78,34)',
                    color:'white',
                    marginLeft:'80px',
                  }}>
                </i>
                <p style={{
                  color:'white',
                  marginLeft:'100px',
                  marginTop:'-15px'
                }}
                onClick={() => {
                  this.props.history.push('/Search');
                }}    
                >
                  寻找宝贝店铺  
                </p>    
              </div>
            ]}
          >
          </NavBar>

          <Carousel
            autoplay = { true }
            infinite
            beforeChange = {(from, to) => console.log(`slide from ${from} to ${to}`) }
            afterChange = { index => console.log('slide to', index)}
          >
            {this.state.data.map(val => ( 
              <a 
                key = { val }
                style = {{ 
                  display: 'inline-block', 
                  width: '100%', 
                  height: this.state.imgHeight,
                  marginTop:'45px'
                }} 
              >
                <img 
                  src = { `https://${val}.jpg` }
                  alt = ""
                  style = {{ width: '100%', verticalAlign: 'top' } }
                  onLoad = {() => {
                    window.dispatchEvent(new Event('resize'));
                    this.setState({ imgHeight: 'auto' });
                  }}
                /> 
              </a>
            ))} 
          </Carousel>  
          <Things/>
          <Mytab/>
        </div>
      );
    }    
}

const mapStateToProps = (state) => ({
  list: state.datalist
})

export default connect(mapStateToProps)(withRouter(Home));