import React from 'react'
import { WingBlank } from 'antd-mobile'
import { withRouter } from 'react-router-dom'
import {datalist} from './actionCreators'
import { connect } from 'react-redux'
class Things extends React.Component {
  constructor(props){
    super(props);
  }
  
  render() {
    this.props.dispatch(datalist())
    return (
      <div>
        <WingBlank>
          {this.props.datalist.map((val,id) => (
            <div style={{
              display:"inline-block",
              marginTop:"15px",
              width:"160px",
              height:"230px",
              marginLeft:"10px",
              borderRadius:"8px",
              backgroundColor:'white'
            }}>
              <img
                src={val.img}
                alt=""
                style={{ 
                  width: '150px', 
                  verticalAlign: 'top',
                  marginBottom:'5px',
                  marginLeft:'5px'
                }}
                onClick={() => {
                  this.props.history.push({
                    pathname:'./pages',
                    id:val.id,
                    index: id,
                    img:val.img,
                    title:val.title,
                    price:val.price,
                    item:val
                  })
                }}             
              />
              <p style={{marginLeft:"15px"}}>{val.title}</p>
              <p style={{marginLeft:"15px"}}>${val.price}</p> 
            </div>
          ))}
          </WingBlank>
        </div>      
    );
  }
}
const mapStateToProps = (state) => ({
  datalist: state.datalist
})

export default connect(mapStateToProps)(withRouter(Things));

